<template>
	<view class="play-page" :class="{'hidder-body': popUpVisible}">
		<rs-header></rs-header>
		<view class="video-container">
			<video class="rs-video" :src="videoSrc" controls danmu-btn show-mute-btn title="一个视频"
				 object-fit="fit" enable-play-gesture></video>
		</view>
		<view class="play-body">
			<view class="info-container bor-bottom">
				<view class="cover-box">
					<u--image radius="4px" :showLoading="true" :src="src" width="80px" height="110px"></u--image>
				</view>
				<view class="intro-box">
					<view class="video-name">凡人修仙传</view>
					<view class="video-volume">
						<u-icon labelSize="14" name="play-circle" label="666万"></u-icon>
						<u-icon labelSize="14" name="heart" label="33.9万"></u-icon>
					</view>
					<view class="video-parts">连载中，每周二更新</view>
					<view class="video-introduction">
						<u-read-more color="#999" ref="uReadMore" :shadowStyle="shadowStyle" toggle showHeight="34"
							closeText="" openText="">
							浔阳江头夜送客，枫叶荻花秋瑟瑟。主人下马客在船，举酒欲饮无管弦。醉不成欢惨将别，别时茫茫江浸月。
							忽闻水上琵琶声，主人忘归客不发。寻声暗问弹者谁，琵琶声停欲语迟。移船相近邀相见，添酒回灯重开宴。千呼万唤始出来，犹抱琵琶半遮面。转轴拨弦三两声，未成曲调先有情。弦弦掩抑声声思，似诉平生不得志。低眉信手续续弹，说尽心中无限事。轻拢慢捻抹复挑，初为《霓裳》后《六幺》。大弦嘈嘈如急雨，小弦切切如私语。嘈嘈切切错杂弹，大珠小珠落玉盘。间关莺语花底滑，幽咽泉流冰下难。冰泉冷涩弦凝绝，凝绝不通声暂歇。别有幽愁暗恨生，此时无声胜有声。银瓶乍破水浆迸，铁骑突出刀枪鸣。曲终收拨当心画，四弦一声如裂帛。东船西舫悄无言，唯见江心秋月白。

						</u-read-more>
					</view>
					<view class="score-wrap">
						<view class="score">
							<text>9.2</text>分
						</view>
						<view class="nums">
							804人
						</view>
					</view>
				</view>
			</view>
			<view class="parts-container bor-bottom">
				<view class="parts-head container-head">
					<view class="head-title">选集</view>
					<view class="tips" @click="popUpVisible = true">
						更新至第6话<u-icon size="12" name="arrow-right"></u-icon>
					</view>
				</view>
				<view class="parts-box">
					<view class="parts-wrap">
						<view @click="choosePart(item)" class="parts-item" v-for="item in 7"
							:class="{'active-part': item == currentPart}">
							<view class="part">
								第5话
							</view>
							<view class="title">
								牡丹
							</view>
						</view>
					</view>
				</view>
				<u-popup :show="popUpVisible" @close="close" @open="open" :overlay='false'>
					<view class="pop-body" :style="{height: videoHeight}">
						<view class="pop-head">
							<text>选集</text>
							<u-icon name="close" @click="popUpVisible = false"></u-icon>
						</view>
						<view class="video-list">
							<view class="item" @click="choosePart(item)" :class="{'active-part': item == currentPart}"
								v-for="item in 7">
								<view class="part">
									第5话
								</view>
								<view class="title">
									牡丹
								</view>
							</view>
						</view>
					</view>
				</u-popup>
				<!-- <u-button @click="show = true">打开</u-button> -->
			</view>
			<view class="recommond-container bor-bottom">
				<view class="container-head">
					<view class="head-title">更多推荐</view>
				</view>
				<view class="recommond-box">
					<view class="recom-item" v-for="item in 6">
						<view class="item-pics">
							<u--image radius="4px" :showLoading="true" :src="src" width="120px" height="76px">
							</u--image>
						</view>
						<view class="item-info">
							<view class="name">阿鲁斯的巨兽</view>
							<view class="infos">494.8万次观看 · 21.2万人追番</view>
						</view>
					</view>
				</view>
			</view>
			<view class="copyright">沪ICP备13002172号-3 bilibili.com © 2009-2020</view>

		</view>

	</view>
</template>

<script>
	//引入 hls与dplayer 用于解析播放视频 
	import Hls from 'hls.js'
	import Dplayer from 'dplayer'
	export default {
		data() {
			return {
				dp: {},
				src: 'https://cdn.uviewui.com/uview/album/1.jpg',
				shadowStyle: {
					backgroundImage: "none",
					paddingTop: "0",
					marginTop: "20rpx"
				},
				popUpVisible: false,
				// 当前第几集
				currentPart: 5,
				videoHeight: '',
				videoSrc: 'https://1259476264.vod2.myqcloud.com/b042c055vodtransgzp1259476264/bed26e03243791579633470978/v.f100210.m3u8'

			}
		},

		watch: {
			popUpVisible() {
				uni.pageScrollTo({
					scrollTop: 0, // 滚动到页面的目标位置  这个是滚动到顶部, 0 
					duration: 100 // 滚动动画的时长
				})

			}
		},
		methods: {
			open() {
				// console.log('open');
			},
			close() {
				this.show = false
				// console.log('close');
			},
			choosePart(number) {
				this.currentPart = number
			}
		}
	}
</script>

<style scoped lang="scss">
	$active-color:#fb7299;

	.play-page {
		padding-bottom: 100px;

		.video-container {
			width: 100%;

			.rs-video {
				width: 100%;
			}
		}

		&.hidder-body {
			height: 70vh;
			overflow: hidden;
		}

		.play-body {

			.bor-bottom {
				border-bottom: 1px solid #e7e7e7;
			}

			.info-container {
				display: flex;
				padding: 0 10px;
				margin-top: 30px;

				.cover-box {}

				.intro-box {
					position: relative;
					flex: 1;
					margin-left: 10px;


					.video {
						&-name {
							font-size: 16px;
						}

						&-volume {
							display: flex;
							color: #999;
							margin: 4px 0;

							.u-icon {
								margin-right: 4px;
							}
						}

						&-parts {
							font-size: 13px;
							color: #999;
							margin-bottom: 4px;
						}

						&-introduction {
							width: 90%;
							font-size: 13px;
							color: #999;
							margin-top: 10px;



							::v-deep .u-read-more__content__inner {
								color: #999;
								font-size: 13px;
							}

							::v-deep .u-read-more__toggle__icon {
								position: absolute;
								right: 0;
								bottom: 16px;
							}


						}
					}

					.score-wrap {
						position: absolute;
						right: 0;
						top: 0;

						.score {
							color: #ffa726;
							font-size: 12px;

							text {
								font-weight: bold;
								font-size: 18px;
							}
						}

						.nums {
							font-size: 13px;
							color: #c0c0c0;
						}
					}
				}


			}

			.container-head {
				height: 45px;
				line-height: 45px;
			}

			.head-title {
				font-weight: 700;
			}

			.parts-container {
				// padding: 10px 0;
				padding: 0 10px;
				padding-bottom: 30px;

				.parts-head {
					display: flex;
					justify-content: space-between;


					.tips {
						display: flex;
						align-items: center;
						color: #999;
						font-size: 12px;
					}
				}

				.parts-box {
					height: 50px;
					overflow-y: hidden;
				}


				.parts-wrap {
					overflow-x: scroll;
					display: flex;
					height: 60px;
				}

				.parts-item {
					flex-shrink: 0;
					width: 120px;
					border: 1px solid #ccc;
					border-radius: 6px;
					height: 46px;
					margin-right: 16px;
					box-sizing: border-box;
					padding: 4px 10px;

					.part {
						font-size: 14px;
						color: #212121;
					}

					.title {
						font-size: 12px;
						color: #505050;
					}
				}
			}

			.recommond-container {
				padding: 0 10px;
				padding-bottom: 10px;

				.recommond-box {
					.recom-item {
						display: flex;
						margin-bottom: 15px;

						.item-pics {}

						.item-info {
							margin-left: 10px;
							flex: 1;
							display: flex;
							flex-direction: column;
							justify-content: space-between;

							.name {
								font-size: 14px
							}

							.infos {
								font-size: 12px;
								color: #999;
							}
						}
					}
				}
			}

		}

		.pop-body {
			height: calc(100vh - 250px);
			box-sizing: border-box;
			padding: 10px;

			.pop-head {
				display: flex;
				justify-content: space-between;
			}

			.video-list {
				margin-top: 20px;
				display: grid;
				grid-template-columns: repeat(2, 1fr);
				grid-gap: 10px;

				.item {
					padding: 8px;
					height: 60px;
					border: 1px solid #ccc;
					border-radius: 6px;

					.part {
						font-size: 14px;
					}

					.title {
						margin-top: 4px;
						font-size: 12px;
					}
				}
			}
		}

		.active-part {
			border-color: $active-color !important;

			.part,
			.title {
				color: $active-color !important;
			}
		}
	}
</style>
